import React, { useState } from 'react';
import AppSearchBar from './AppSearchBar';
import CssBaseline from '@mui/material/CssBaseline';
import ActivityCard from './ActivityCard';
import Grid2 from '@mui/material/Unstable_Grid2';
import { searchForCharacter } from './DestinyAPI';

function Results(){
    const searchParams = new URLSearchParams(window.location.search);
    const location = searchParams.get("n");
    const bungieName = location + window.location.hash;

    searchForCharacter(bungieName);

    return (
        <>
        <CssBaseline/>
        <AppSearchBar />
        <Grid2 container rowSpacing={5} spacing={8} sx={{p:6, width: 1}}>
            {Array.from(Array(14)).map((_, index) => (
                <Grid2 key={index}>
                    <ActivityCard data={bungieName}/>
                </Grid2>
            ))}
        </Grid2>
        </>
    );
  };

export default Results;